import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Drawer, List, ListItem, ListItemIcon, ListItemText, Toolbar, makeStyles } from '@material-ui/core';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import host, { getFiles, getFolderList } from "../api";

const drawerWidth = 300;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerContainer: {
    overflow: 'auto',
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing(3),
  },
}));

function FileManager({ files, setFiles }) {
  const classes = useStyles();

  const [folders, setFolders] = useState([]);
  const token = localStorage.getItem('token');

  useEffect(() => {
    getFolderList()
      .then(response => response.data)
      .then(data => setFolders(data))
      .catch(error => console.log(error));
  }, []);


  const handleFolder = (e) => {
    console.log(e)

    if (e) {
      getFiles(e)
        .then(response => response.data)
        .then(data => setFiles(data))
        .catch(error => console.log(error));
    }
  }



  return (
    <div>
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <Toolbar />
        <div className={classes.drawerContainer}>
          <List>
            {folders.map((text, index) => (
              <ListItem button key={text.filename} onClick={() => handleFolder(text.filename)}>
                <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
                <ListItemText primary={text.filename + " (" + text.size + ")"} />
              </ListItem>
            ))}
          </List>
        </div>
      </Drawer>
    </div>
  );
}

export default FileManager;