import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import VideoView from './VideoView';
import host from '../api';
import ImageView from './ImageView';

const useStyles = makeStyles({
    card: {
        maxWidth: 200,
        display: "flex",
        minWidth: 150,
        borderRadius: 12
    },
    media: {
        height: 140,
    },
});

export default function ContentCard({ data }) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [openImage, setOpenImage] = React.useState(false);
    const handleOpen = () => {
        if (data.contentType === "Video")
            setOpen(true)
        if (data.contentType === "Image")
            setOpenImage(true)
    }
    const handleClose = () => {
        if (data.contentType === "Video")
            setOpen(false)
        if (data.contentType === "Image")
            setOpenImage(false)
        console.log("close")
    }


    return (
        <Card className={classes.card} onClick={handleOpen}>
            <CardActionArea>
                <CardMedia
                    className={classes.media}
                    image={host + "/thumb?uri=" + data.uri}
                    title="Contemplative Reptile"
                />
                <CardContent>
                    <Typography variant="body2" component="p">
                        {data.fileName}
                    </Typography>
                    <Typography variant="label" color="textSecondary" component="p">
                        Size:{data.size}
                    </Typography>
                </CardContent>
            </CardActionArea>
            <VideoView data={data} open={open} handleClose={handleClose} />
            <ImageView data={data} open={openImage} handleClose={handleClose} />
        </Card>
    );
}
